*{
  box-sizing: border-box;
  font-family:Arial, Helvetica, sans-serif;
  border-color:#1d2129;
  border-width: 0.5px;
  border-style: solid;
}

body {
  margin: 0;
  background: #e9ebee;
  color: #1d2129;
  text-align: left;
  font-size: 3.5vw;

}
 


.Header{
  color: rgb(255, 255, 255);
  padding: 20px;
  margin-left: 0px;
  background: #b24141;
}

.Game{
  margin: 20px;
  font-size: 40px;
}

.NavBar{
  margin: 20px;
  font-size: 40px;
}

.NavBar select{
  font-size: 20px;
  margin: 5px;
}

.NavBar button{
  font-size: 20px;
  margin: 5px;
}

.List{
  line-height: 60px;

}


.App{
  margin: 20px;
  padding: 20px;
}

.App h1{
  font-size: 5vw;
  margin: 0px;
}



.Access{
  margin: 20px;
}

.Footer{
  font-size: 3vw;
  margin: 20px;
}

